import styled from "styled-components";
import { mainStyle } from "./Style/Globalstyle";
import me from "./images/me.png";
import { Link } from "react-router-dom";
import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";

const AboutmeWrap = styled.div`
  @media screen and (max-width: 500px) {
    width: 100%;
  }
`;

const BoxWrap = styled.div`
  padding: ${mainStyle.padding};
  display: flex;
  align-items: center;
  margin-top: 70px;
  @media screen and (max-width: 500px) {
    padding: 0;
    flex-direction: column;
  }
`;

const Left = styled.div`
  width: 600px;
  height: 600px;
  @media screen and (max-width: 500px) {
    width: 300px;
    height: 300px;
  }
`;

const Right = styled.div`
  width: 800px;
  margin-left: 80px;
  @media screen and (max-width: 500px) {
    width: 90%;
    margin-left: 0;
  }
`;
const AboutCon = styled.h3`
  h4 {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 5px;
  }
  p {
    font-size: 20px;
    line-height: 30px;
    font-weight: 300;
  }
  margin-bottom: 50px;
  @media screen and (max-width: 500px) {
    h4 {
      font-size: 24px;
      line-height: 30px;
      margin: 10px 0;
      text-align: center;
    }
    p {
      font-size: 15px;
      line-height: 24px;
    }
  }
`;

const Future = styled.p`
  h5 {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 10px;
  }

  font-size: 20px;
  line-height: 30px;
  font-weight: 300;
  margin-bottom: 50px;
  @media screen and (max-width: 500px) {
    h5 {
      font-size: 24px;
      text-align: center;
    }
    font-size: 15px;
    line-height: 24px;
  }
`;

const Study = styled.div`
  h5 {
    font-size: 22px;
    font-weight: 500;
    margin-bottom: 10px;
  }

  font-size: 20px;
  line-height: 30px;
  font-weight: 300;
  @media screen and (max-width: 500px) {
    h5 {
      font-size: 24px;
      text-align: center;
    }
    font-size: 15px;
    line-height: 24px;
  }
`;

const SkillWrap = styled.div`
  margin-top: 120px;
  height: 80vh;
  padding: ${mainStyle.padding};
  text-align: center;
  @media screen and (max-width: 500px) {
    padding: 0 20px;
    text-align: center;
  }
`;
const Skill = styled.div`
  font-size: 40px;
  font-weight: 700;
  h3 {
    font-family: "Black Han Sans", sans-serif;
  }
  @media screen and (max-width: 500px) {
  }
`;

const Lang = styled.div`
  margin-top: 50px;
  img {
    width: 180px;
    height: 200px;
    margin-right: 30px;
    :nth-child(5) {
      margin-right: 0;
    }
  }
  p {
    font-size: 25px;
    line-height: 20px;
    font-weight: 300;
  }
  .first {
    margin-top: 70px;
  }
  span {
    font-weight: 500;
    color: ${mainStyle.point};
  }
  @media screen and (max-width: 500px) {
    img {
      width: 130px;
      height: 130px;
      margin-right: 20px;
      margin-bottom: 20px;
    }
    p {
      font-size: 22px;
      line-height: 25px;
    }
  }
`;

const ImgTool = styled.div`
  margin-top: 100px;
  font-size: 40px;
  font-weight: 700;
  img {
    width: 180px;
    height: 200px;
    margin-top: 70px;
    :nth-child(2) {
      margin-right: 30px;
    }
  }
  p {
    margin-top: 70px;
    font-size: 25px;
    line-height: 20px;
    font-weight: 300;
  }

  span {
    font-weight: 500;
    color: ${mainStyle.point};
  }
  @media screen and (max-width: 500px) {
    margin-top: 120px;
    img {
      width: 130px;
      height: 130px;
    }
    p {
      font-size: 22px;
      line-height: 25px;
    }
  }
`;

const ProjectGo = styled.div`
  text-align: center;
  margin-top: 600px;
  @media screen and (max-width: 500px) {
    margin-top: 1100px;
  }
`;

const ProjectScroll = styled.div`
  text-align: center;
  font-size: 26px;
  margin-bottom: 150px;
  font-family: "Black Han Sans", sans-serif;
  :hover {
    color: ${mainStyle.point};
    cursor: pointer;
  }
  @media screen and (max-width: 500px) {
  }
`;

