import styled from "styled-components";
import { mainStyle } from "./Style/Globalstyle";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import "swiper/css/navigation";
import "swiper/css";
import React, { useEffect } from "react";
import AOS from "aos";
import "aos/dist/aos.css";

import enjoy from "./images/yjmovie.png";
import login from "./images/yjlogin.png";
import daelim from "./images/daelim.png";
import paikdabang from "./images/paikdabang.png";
import dyson from "./images/dyson.png";
import rsp from "./images/rsp.png";

const ProjectWrap = styled.div`
  padding: ${mainStyle.padding};
  @media screen and (max-width: 500px) {
    padding: 0 10px 0 10px;
  }
`;

const Title = styled.h3`
  font-family: "Black Han Sans", sans-serif;
  margin: 100px 0;
  font-size: 50px;

  text-align: center;
  span {
    font-size: 65px;
    color: ${mainStyle.point};
  }
  @media screen and (max-width: 500px) {
    font-size: 40px;
    span {
      font-size: 45px;
    }
  }
`;
const Projects = styled.div`
  display: flex;
  justify-content: space-between;
  @media screen and (max-width: 500px) {
    flex-direction: column;
    width: 100vw;
  }
`;

const SwiperWrap = styled.div`
  display: flex;
  margin-bottom: 50px;
  @media screen and (max-width: 500px) {
    flex-direction: column;
  }
`;

const ProjectCon = styled.div`
  width: 45%;
  padding: 0 60px;
  @media screen and (max-width: 500px) {
    padding: 0;
    width: 100%;
    text-align: center;
  }
`;

const ProjectTitle = styled.h1`
  font-size: 60px;
  font-weight: 900;
  margin-bottom: 20px;
  @media screen and (max-width: 500px) {
    font-size: 30px;
  }
`;
const Team = styled.p`
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 50px;
  @media screen and (max-width: 500px) {
    font-size: 18px;
  }
`;

const ProjectImg = styled.div`
  width: 600px;
  height: 500px;
  background-color: #1d1d1d;
  @media screen and (max-width: 500px) {
    width: 370px;
    height: 240px;
  }
`;

const ProjectDetail = styled.div`
  width: 45%;
  font-size: 24px;
  font-weight: 500;
  line-height: 35px;
  @media screen and (max-width: 500px) {
    font-size: 16px;
    line-height: 25px;
    width: 100%;
    text-align: center;
  }
`;
const DetailCon = styled.h3`
  width: 90%;
  margin-top: 135px;
  @media screen and (max-width: 500px) {
    width: 90%;
    margin-left: 10px;
  }
`;

const Skill = styled.div`
  margin: 50px 0;
  font-size: 30px;
  p {
    font-size: 20px;
    margin-top: 10px;
  }
  @media screen and (max-width: 500px) {
    font-size: 20px;
    margin-right: 20px;
    p {
      font-size: 16px;
    }
  }
`;

const Link = styled.div`
  display: flex;
  font-size: 40px;
  @media screen and (max-width: 500px) {
    justify-content: center;
  }
`;
const Site = styled.div`
  margin-right: 30px;
  transition: 0.5s;
  cursor: pointer;
  :hover {
    color: ${mainStyle.point};
  }
  @media screen and (max-width: 500px) {
    font-size: 25px;
    margin-right: 15px;
  }
`;
const Github = styled.div`
  transition: 0.5s;
  cursor: pointer;
  :hover {
    color: ${mainStyle.point};
  }
  @media screen and (max-width: 500px) {
    font-size: 25px;
  }
`;

